.card-container {
    cursor: pointer;
    height: 300px;
    width: 300px;
    -webkit-perspective: 800px;
            perspective: 800px;
    position: relative;
    margin: auto;
    -webkit-transition: ease-in;
    -o-transition: ease-in;
    transition: ease-in;
    margin-top: 5%;
  }
  
  .card {
    height: 300px;
    width: 300px;
    position: absolute;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
  
  .card:hover {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    -webkit-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
  
  .card .front {
    height: 300px;
    width: 300px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    position: absolute;
    overflow: hidden;
    text-align: center;
    background: #F5F5F5;
    border-radius: 3%;
    -webkit-box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
            box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
  }
  
  .card .back {
    height: 300px;
    width: 300px;
    text-align: center;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    background: #F5F5F5;
    border-radius: 3%;
    -webkit-box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
            box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
  }
  
  .eng{
    color: #333;
    font-size: 2.3em;
    padding-top: 40%;
  }
  
  .han{
    color: #333;
    font-size: 6.3em;
    padding-top: 20%;
  }
  
  .pin{
      color: rgba(200, 10, 10, 1);
      font-size: 2.3em;
      padding-top: 10%;
  }


